import React from "react";
import { Link } from "react-router-dom";

import { useAppDispatch, useAppSelector } from "../../hooks";
import { SectionTitle, WishListItem } from "../../components";
import { selectWishList } from "../../redux/wishList/selectors";
import { EmptyWishList } from "../../components/EmptyWishList";
import { clearWishList } from "../../redux/wishList/slice";

import s from "./WishList.module.scss";

import arrow from "../../assets/img/backArrowIcon.svg";

const WishList: React.FC = () => {
  const dispatch = useAppDispatch();
  const { items } = useAppSelector(selectWishList);
  const onClear = () => {
    if (window.confirm("Clear?")) {
      dispatch(clearWishList());
    }
  };
  const wishListItemsElem = (
    <>
      <div className={s.header}>
        <Link to="/" className={`${s.linkToHome} ${s.headerItem}`}>
          <img src={arrow} alt="back" />
          BACK TO HOME
        </Link>
        <button
          onClick={onClear}
          className={`${s.clearButton} ${s.headerItem}`}
        >
          CLEAR
        </button>
      </div>
      <div className={s.items}>
        {items.map((obj) => (
          <WishListItem key={obj.id} {...obj} />
        ))}
      </div>
    </>
  );

  return (
    <div className={s.page}>
      <div className={`${s.container} container`}>
        <SectionTitle title="WISH LIST" className={s.title} />
        {items.length ? wishListItemsElem : <EmptyWishList />}
      </div>
    </div>
  );
};

export default WishList;
