import React from "react";

import s from "./EmptyWishList.module.scss";

export const EmptyWishList: React.FC = () => {
  return (
    <div className={s.body}>
      <h1 className={s.title}>YOUR WISH LIST IS EMPTY</h1>
      <p className={s.message}>
        Add Something To Your Wish List And Come Here Again!
      </p>
    </div>
  );
};
